// eslint-disable-next-line no-extend-native
String.prototype.onHighlightText = function (text, callback) {
    if (text) {
        let regex = new RegExp(text.trim().toLowerCase(), "gi");

        let highlightedItem = this.replace(
            regex,
            `<span class='highlight'>${callback ? callback(text) : "$&"}</span>`
        );

        return highlightedItem;
    }
    return this;
};
// eslint-disable-next-line no-extend-native
String.prototype.removeSpecialCharacters = function () {
    const pattern = /[,./;'[\]\=-`\\\/<>?:"{}|+_\(\)*&^%$#@!~]/g; // Biểu thức chính quy để tìm các ký tự đặc biệt

    return this.replace(pattern, ""); // Loại bỏ các ký tự đặc biệt trong chuỗi
};

// test = false

const LOCALHOST = "localhost";
const hostName = window.location.hostname;

export const isNotTestMode = hostName.includes(LOCALHOST) ? false : false; // developing : production

